import { JSX } from "react"
import { useParams } from "react-router-dom"
import { useAccount } from "@rp/react/common/providers"
import { useCreateSavedQuery } from "@rp/react/tanstack-api/user-data-queries"
import { LoadingSpinner, PageWrapperStyled } from "@rp/react/web/common/base-components"
import {
  HubWatchlistsToggleTabs,
  MOBILE_FAB_B_PADDING,
  MaxWidthContainer,
} from "@rp/react/web/common/composite-components"
import { HtmlPageHead } from "@rp/react/web/common/header-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, styled } from "@rp/react/web/mui/core"
import { Add } from "@rp/react/web/mui/icons"
import { useWatchlistsFilterTerm, useWatchlistsTabNavigation } from "@rp/react/web/watchlists"
import { HubHeader } from "../../components/Hub/HubHeader/HubHeader"
import { HeaderProps } from "../../components/Hub/HubHeader/HubHeader.types"
import { FindResultWatchlists } from "../../components/Hub/Watchlists/FindResultWatchlists/FindResultWatchlists"
import { PublicWatchlists } from "../../components/Hub/Watchlists/PublicWatchlists"
import { UserWatchlists } from "../../components/Hub/Watchlists/UserWatchlists"

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

const TabPanelStyled = styled("div", {
  name: "TabPanelStyled",
  shouldForwardProp: prop => prop !== "hidden",
})(({ hidden }) => ({
  flexGrow: 1,
  overflow: "auto",
  display: hidden ? "none" : "flex",
}))

const TabPanel = (props: TabPanelProps): JSX.Element => {
  const { children, value, index, ...other } = props

  return (
    <TabPanelStyled
      role="tabpanel"
      hidden={value !== index}
      id={`hub-tabpanel-${index}`}
      aria-labelledby={`hub-tab-${index}`}
      {...other}
    >
      {value === index && children}
    </TabPanelStyled>
  )
}

export const WatchlistsListView = () => {
  const { tabNumValue, handleNumChange } = useWatchlistsTabNavigation()
  const { filterTerm, setFilterTerm } = useWatchlistsFilterTerm()
  const { id = "" } = useParams()
  const { isMobile } = useDeviceInfo()
  const createQuery = useCreateSavedQuery()
  const { canShare } = useAccount()

  const isLoading = createQuery.isPending || createQuery.isSuccess

  const headerConfig: HeaderProps = {
    placeholder: "Find your watchlists",
    label: "Watchlists",
    buttonLabel: "New Watchlist",
    onClick: "createWatchlist",
    icon: {
      Icon: Add,
    },
  }

  if (isLoading) {
    return <LoadingSpinner />
  }

  return (
    <PageWrapperStyled>
      <HtmlPageHead title="Watchlists" />
      <HubHeader
        {...headerConfig}
        filterTerm={filterTerm}
        setFilterTerm={setFilterTerm}
        onClick={headerConfig.onClick}
        id={id}
        page="Watchlists"
      />
      {filterTerm.length > 0 ? (
        <MaxWidthContainer
          sx={{
            paddingBottom: isMobile ? MOBILE_FAB_B_PADDING : null,
          }}
        >
          <FindResultWatchlists filterTerm={filterTerm} />
        </MaxWidthContainer>
      ) : (
        <>
          {canShare ? (
            <MaxWidthContainer
              sx={{
                paddingX: isMobile ? 0 : "inherit",
                flexGrow: 0,
              }}
            >
              <Box
                sx={{
                  position: "relative",
                  paddingTop: 2,
                  px: isMobile ? 2 : 0,
                }}
              >
                <HubWatchlistsToggleTabs
                  size="small"
                  value={tabNumValue}
                  setValue={handleNumChange}
                />
              </Box>
            </MaxWidthContainer>
          ) : null}
          <MaxWidthContainer sx={{ paddingBottom: MOBILE_FAB_B_PADDING }}>
            <TabPanel value={tabNumValue} index={0}>
              <UserWatchlists />
            </TabPanel>
            {canShare ? (
              <TabPanel value={tabNumValue} index={1}>
                <PublicWatchlists />
              </TabPanel>
            ) : null}
          </MaxWidthContainer>
        </>
      )}
    </PageWrapperStyled>
  )
}
